*{
    box-sizing: border-box;
    margin: 0;
}

@font-face {
	font-family: 'Quicksand-Bold';
	src: url('../../fonts/quicksand/Quicksand-Bold.eot');
    src: url('../../fonts/quicksand/Quicksand-Bold.woff') format('woff'), 
        url('../../fonts/quicksand/Quicksand-Bold.ttf') format('truetype'), 
        url('../../fonts/quicksand/Quicksand-Bold.svg') format('svg');
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: 'Montserrat-Regular';
	src: url('../../fonts/montserrat/Montserrat-Regular.eot');
    src: url('../../fonts/montserrat/Montserrat-Regular.woff') format('woff'), 
        url('../../fonts/montserrat/Montserrat-Regular.ttf') format('truetype'), 
        url('../../fonts/montserrat/Montserrat-Regular.svg') format('svg');
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: 'Montserrat-Bold';
	src: url('../../fonts/montserrat/Montserrat-Bold.eot');
    src: url('../../fonts/montserrat/Montserrat-Bold.woff') format('woff'), 
        url('../../fonts/montserrat/Montserrat-Bold.ttf') format('truetype'), 
        url('../../fonts/montserrat/Montserrat-Bold.svg') format('svg');
	font-weight: normal;
	font-style: normal;
}

@font-face {
    font-family: 'Material Icons';
    font-style: normal;
    font-weight: 400;
    src: url('../../fonts/material-icons/MaterialIcons-Regular.eot'); /* For IE6-8 */
    src: local('Material Icons'),
         local('MaterialIcons-Regular'),
         url('../../fonts/material-icons/MaterialIcons-Regular.woff2') format('woff2'),
         url('../../fonts/material-icons/MaterialIcons-Regular.woff') format('woff'),
  }
  
  .material-icons {
    font-family: 'Material Icons';
    font-weight: normal;
    font-style: normal;
    font-size: 24px;  /* Preferred icon size */
    display: inline-block;
    line-height: 1;
    text-transform: none;
    letter-spacing: normal;
    word-wrap: normal;
    white-space: nowrap;
    direction: ltr;
  
    /* Support for all WebKit browsers. */
    -webkit-font-smoothing: antialiased;
    /* Support for Safari and Chrome. */
    text-rendering: optimizeLegibility;
  
    /* Support for Firefox. */
    -moz-osx-font-smoothing: grayscale;
  
    /* Support for IE. */
    font-feature-settings: 'liga';
  }